@font-face {
  font-family: 'Cinnamon Coffe Coke';
  src: url("assets/CinnamonCoffeCake.ttf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  width: 100%;
  height: auto;
  min-height: 100vh;
}

/* Navigation bar styling */
#root > nav {
  /* container */
  width: 42px;
  height: inherit;
  padding: 7px;
  overflow: hidden;
  white-space: nowrap;
  flex-shrink: 0;
  /* appearance */
  background: rgb(2,0,36);
  background: linear-gradient(65deg, rgba(196,113,113,1) 0, rgba(2,0,36,1) 95%);
  font-size: 22px;
  /* animation */
  transition: 300ms;
}

#root > nav:hover, #root > nav:focus {
  outline: none;    /* don't show border when focused */
  width: 250px;
}

#root > nav > ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#root > nav > ul > li {
  margin-bottom: 10px;
}

#root > nav > ul > li > div {
  font-family: "Cinnamon Coffe Coke", serif;
  cursor: pointer;
  /* container */
  display: flex;
  align-items: center;
  padding: 0;
  width: 100%;
  /* appearance */
  font-size: 24px;
  color: white;
}

#root > nav > ul > li > div > img {
  max-width: 40px;
  transition: all 50ms;
}

#root > nav > ul > li > div > img.nav-item-selected {
  max-width: 37px;
  border: 2px solid #ff6b87;
}

#root > nav > ul > li > div > button {
  /* override default */
  background-color: transparent;
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  cursor: inherit;
  /* container */
  flex-grow: 1;
  margin: 0 8px;
  padding: 5px;
  /* animation */
  transition: 100ms;
}

#root > nav > ul > li > div > button:hover, #root > nav > ul > li > div > button.nav-item-selected {
    background-color: rgba(106, 90, 205, 0.30);
    background-image: url("assets/nav-bar/hover.gif");
}

/* This is for tab users. It has to be disabled for mobile devices,
   because they don't support focus-visible. */
@media (hover: hover) {
    #root > nav:has(button:focus-visible) {
        outline: none;    /* don't show when focused */
        width: 250px;
    }

    #root > nav > ul > li > div > button:focus-visible {
        background-color: rgba(106, 90, 205, 0.30);
        background-image: url("assets/nav-bar/hover.gif");
    }
}

#root > main {
  flex-grow: 1;
  height: inherit;
}

#root > main > header {
  height: 40px;
  width: 100%;
  background-image:
    linear-gradient(0, rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)),
    url("assets/header.jpg");
  background-position-y: center;
  color: white;
  font-family: "Cinnamon Coffe Coke", serif;
  display: flex;
}

#root > main > header > div.jukebox {
  height: inherit;
  display: flex;
}

#root > main > header > div.jukebox > button {
  border: none;
  background: transparent;
  margin: 5px 0 5px 8px;
    cursor: pointer;
}

#root > main > header > div.jukebox > button > img {
    height: 20px;
}

#root > main > header > div.jukebox > button > img:hover {
    opacity: 0.85;
}

#root > main > header > div.jukebox > input[type="range"] {
    margin-left: 11px;
    width: 100px;
    accent-color: #ff6b87;
}

#root > main > header > div.jukebox > p {
    margin: 4px;
}
#root > main > header > div.page-title {
    flex-grow: 1;
}

#root > main > header > div.page-title > p {
    text-align: right;
    font-size: 23px;
    margin: 4px 20px;
}

@media (hover: none) {
    #root > main > header > div.page-title {
        display: none;
    }
    #root > main > header > div.jukebox > input[type="range"] {
        display: none;
    }
    #root > main > header > div.jukebox > p {
        overflow: hidden;
    }
}

#root > main > div.main {
    background-attachment: fixed;
    min-height: calc(100vh - 40px);
    background-image:
      linear-gradient(0, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
      url("assets/background.jpg");
    background-size: cover;
    margin-top: -1px;
    padding-top: 1px;
}

#root > main > div.main > div.content, #root > main > div.main > div.table {
    background: linear-gradient(0, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
    border: 3px solid gray;
    border-radius: 25px;
    color: white;
    min-height: 78vh;
    vertical-align: top;
    padding: 17px 22px;
    display: inline-block;
}

#root > main > div.main > div.content {
    margin: 3% 0 0 4%;
    width: 65%;
    overflow: hidden;
}

@media (hover: none) {
    #root > main > div.main > div.content {
        width: 60%;
        font-size: 16px;
    }
}

#root > main > div.main > div.content > h1 {
    margin: 6px 0 22px 10px;
    font-size: 27px;
}

#root > main > div.main > div.content > h2 {
    font-size: 22px;
    margin: 0;
}

#root > main > div.main > div.content > p {
    margin: 10px 0 18px 0;
}

#root > main > div.main > div.table {
    margin: 3% 0 0 3%;
    width: 18%;
    overflow: hidden;
}

#root > main > div.main > div.table > ol {
    margin: 0;
    padding: 0;
    font-family: inherit;
    list-style-type: none;
}

#root > main > div.main > div.table > ol > li {
    counter-increment: step-counter;
    display: flex;
    font-size: 18px;
    padding: 4px 0;
}

#root > main > div.main > div.table > ol > li:before {
    content: counter(step-counter) ".";
    margin-right: 6px;
}

#root > main > div.main > div.table > ol > li > button {
    align-items: normal;
    background: transparent;
    border: none;
    font-size: inherit;
    margin: 0;
    padding: 0;
    color: inherit;
    font-family: inherit;
    text-align: left;
}

#root > main > div.main > div.table > ol > li > button:hover {
    text-decoration: underline;
}

#root > main > div.main > div.table > ol > li > button:active {
    opacity: 0.6;
}

#root > main > div.main > div.table > ol > li > button.selected {
    font-weight: bold;
}